:root {
  --background-colour: #fafafa;
  --perimeterx-brand-colour: #ce0e2d;
  --text-color: #454545;
  --block-padding: 2em;
}

* {
  box-sizing: border-box;
  border: 0;
  margin: 0;
  padding: 0;
}

html {
  height: 100%;
}

body {
  height: 100%;
  max-height: 100%;
  background: var(--background-colour) url("assorted-color-umbrella.96b1e151.jpg") fixed top center no-repeat;
  color: var(--text-color);
  background-size: 100%;
  grid-template-rows: auto 70vh;
  display: grid;
  position: relative;
}

body, input, select, button, textarea {
  font: 100 100% / 1.8 -apple-system, BlinkMacSystemFont, Helvetica Neue, Helvetica, Lucida Grande, Arial, sans-serif;
}

header, main {
  display: flex;
}

header {
  text-align: center;
  color: #fff;
  text-shadow: -3px 3px #000c;
  background: #0000004d;
  flex-direction: column;
  justify-content: center;
  box-shadow: inset 0 -10px 10px #00000080;
}

label {
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
}

header label {
  background: var(--background-colour);
  color: #000;
  text-shadow: none;
  border-radius: 1em;
  margin: .25em;
  padding: .25em 1em;
  display: inline-block;
}

section, aside {
  background: var(--background-colour);
  min-width: 33.3333vw;
  width: 100%;
  transition: all .2s ease-out;
  overflow: auto;
  box-shadow: 0 0 30px -20px #000;
}

section {
  border: var(--block-padding) solid var(--background-colour);
}

aside {
  min-width: 30vw;
  white-space: pre-line;
  border: var(--block-padding) solid black;
  background: #000;
  font-family: monospace;
}

.instructions .instructions, .logs .logs, .no-instructions .instructions, .no-logs .logs {
  width: 0;
  min-width: 0;
  border-width: 0;
}

@media (max-width: 1024px) {
  main {
    flex-direction: column;
  }

  section, aside {
    min-width: 100vw;
  }

  .no-instructions .instructions, .no-logs .logs {
    display: none;
  }
}

@keyframes color_change {
  from {
    color: #0f0;
  }

  to {
    color: #afafaf;
  }
}

aside p {
  color: #afafaf;
  border-bottom: 1px solid #4b4b4b;
  padding-bottom: .25em;
  animation: 10s color_change;
}

aside p.debug {
  filter: brightness(.75);
}

aside p:nth-child(17) {
  color: #7d7d7d;
}

aside p:nth-child(18) {
  color: #646464;
}

aside p:nth-child(19) {
  color: #4b4b4b;
}

aside p:nth-child(20) {
  color: #191919;
}

aside p:nth-child(n+20), .no-debug aside p.debug {
  display: none;
}

a {
  color: inherit;
}

form {
  transition: filter .2s;
}

.loading form {
  filter: opacity(.5);
}

input, select, button, textarea {
  background: #fff;
  border: 1px solid #ccc;
}

input {
  text-indent: .25em;
}

input:invalid {
  border-color: var(--perimeterx-brand-colour);
  background: #ffeaa7;
}

button {
  cursor: pointer;
  width: 80%;
  border-radius: 5px;
  margin: .5em auto;
  display: block;
}

button:hover {
  border-color: var(--text-color);
  background: var(--background-colour);
}

form label {
  border-bottom: 1px solid #0000001a;
  padding-bottom: .5em;
  display: block;
}

textarea {
  width: 100%;
  max-width: 100%;
  height: 7em;
}

h3, p, label {
  margin: 0 0 .5em;
}

dialog.my-challenge-popup {
  font-size: .9em;
}

dialog.my-challenge-popup > div {
  border: 1px solid #000;
  border-radius: 3px;
}

dialog.my-challenge-popup .title {
  font-weight: bold;
}

a.repo {
  z-index: 23456;
  background: #fff;
  border-radius: 50%;
  padding: .25em;
  transition: transform .3s ease-out;
  position: fixed;
  top: 1em;
  right: 1em;
}

a.repo:hover {
  transform: rotate(360deg);
}

.super-high-z-index {
  width: 1px;
  height: 1px;
  z-index: 2000000000;
  position: absolute;
  top: 0;
  left: -1px;
}

